<template lang="pug">
    .content-wrapper
        table.ui-table
            thead
                tr
                    th
                        span Name
                    th
                        span Gender
                    th
                        span Age
            tbody
                tr
                    td
                        span.ui-text-regular Tomas
                    td
                        span.ui-text-regular Male
                    td
                        span.ui-text-regular 26
        button.btn-default(
            @click="getUsers"
        ) Get users
</template>

<script>
export default {
  methods: {
    getUsers () {
      console.log(this.getGetter)
    }
  },
  computed: {
    getGetter () {
      return this.$store.getters.usersCount
    }
  }
}
</script>

<style lang="stylus" scoped>
button
    color blue
</style>



